<template>
  <div id="app">
    <!--头部轮播图-->
		<header class="header">
			<!-- 轮播图 -->
			<div class="swiper-container">
				<div class="swiper-wrapper">
					<div class="swiper-slide"><img src="/static/img/1-1FI01SF4D1.jpg"></div>
					<div class="swiper-slide"><img src="/static/img/1-1FI01SF4D1.jpg"></div>
					<div class="swiper-slide"><img src="/static/img/1-1FI01SF4D1.jpg"></div>
				</div>
				<!-- 分页 -->
				<div class="swiper-pagination"></div>
			</div>
		</header>
    <marquee direction="left" scrolldelay="100" width="100%" onmouseover="this.stop()" onmouseout="this.start()" scrollamount="10" style="height: 25px; line-height: 25px; color: rgb(58, 160, 246); white-space: nowrap; width: 100%;">
			<p style="color:#3AA0F6;">友情提醒：每天15：00-19:00任务最多，记得来抢哦！如遇问题请添加客服微信:1115009958</p>
	    </marquee>
	    <!--快速导航部分-->
	    <div class="nav">
	    		<router-link to="#">苹果</router-link>
	    		<router-link to="#">安卓</router-link>
	    		<router-link to="#">资讯</router-link>
	    </div>
    <router-view/>
  </div>
</template>

<script>
  import 'swiper/dist/css/swiper.css'
	import Swiper from 'swiper'
export default {
  name: 'App',
  mounted: function() {
			// 轮播图
			new Swiper('.swiper-container', {
				loop: true, //无限轮播
				// 如果需要分页器
				pagination: {
					el: '.swiper-pagination',
				},
				autoplay: {
					delay: 3000, //轮播时间
					disableOnInteraction: false, //用户操作后是否停止自动切换
				}
			})
		}
}

</script>

<style>
html,body,#app{
	width: 100%;
	height: 100%;
	overflow: hidden;
	overflow-y: auto;
}
.swiper-container img {
	width: 100%;
}
/*<!--快速导航部分-->*/

.nav {
	display: flex;
	height: 34px;
	margin: 0 10px;
	border-radius: 5px;
	background-color: #00a0fd;
}

.nav a {
	display: inline-block;
	flex: 1;
	text-align: center;
	line-height: 34px;
	text-decoration: none;
	color: #FFFFFF;
	border-right: 1px solid #eee;
}

.nav a:last-child{
	border: none;
}
</style>



