<template>
  <div class="iphone">
    <!--tab切换-->
		<div class="tab">
			<router-link to="#" class="hot weui-tabbar__item weui-bar__item_on">热门排行</router-link>
			<router-link to="#" class="more weui-tabbar__item ">推荐更多</router-link>
		</div>
    <div class="weui-tab__panel">
      <div class="tab-content">
						<div class="list" v-for="(item,index) in list " :key="index" @click="see(item.shiwan_url)">
							<div class="left-img">
								<img :src="item.pic_url"/>
							</div>
							<div class="right-txt">
								<div class="t1" style="color: red;">{{item.title}}</div>
								<div class="t2">{{item.info}}</div>
								<div class="t3">{{item.person}}人加入</div>
								<div class="t4">进入</div>
							</div>
						</div>
      </div>
      <div class="tab-content" style="display:none">2</div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "Iphone",
  data() {
    return {
      list:[]
    };
  },
  methods:{
    see(e){
        window.location.href = e
    }
  },
  created(){
    axios.get("https://chenxuba.github.io/shiwan.json").then((res)=>{
      this.list = res.data
      // console.log(this.list)

    })
  },
  mounted(){
      $(function() {
  $(".weui-tabbar__item").on("click", function() {
    $(this)
      .addClass("weui-bar__item_on")
      .siblings(".weui-bar__item_on")
      .removeClass("weui-bar__item_on");

    var index = $(this).index();

    $(".tab-content")
      .hide()
      .eq(index)
      .show();
    console.log(index);
  });
});
  }
};

</script>

<style scoped>
/*<!--快速导航部分-->*/

.nav {
  display: flex;
  height: 34px;
  margin: 0 10px;
  border-radius: 5px;
  background-color: #00a0fd;
}

.nav a {
  display: inline-block;
  flex: 1;
  text-align: center;
  line-height: 34px;
  text-decoration: none;
  color: #ffffff;
  border-right: 1px solid #eee;
}

.nav a:last-child {
  border: none;
}
/*<!--tab切换-->*/

.tab {
  height: 34px;
  display: flex;
  text-align: center;
  line-height: 34px;
  border: 1px solid #00a0fd;
  margin: 0 10px;
  border-radius: 5px;
  background: #ffffff;
  margin-top: 15px;
  margin-bottom: 15px;
}

.tab a {
  text-decoration: none;
}

.tab .hot {
  flex: 1;
  background: #fff;
  color: #00a0fd;
  border-radius: 5px 0px 0px 5px;
}

.tab .more {
  flex: 1;
  background: #fff;
  color: #00a0fd;
  border-radius: 0 5px 5px 0;
}

.tab .weui-bar__item_on {
  background: #00a0fd;
  color: #ffffff;
  /*border-radius: 5px;*/
}

.weui-tab__panel {
	/*width: 100%;*/
	height: 100%;
	/*background: cyan;*/
	margin: 0 10px;
}

.tab-content {
	height: 100%;
}

.tab-content a {
	display: block;
	text-decoration: none;
}

.tab-content .list {
	padding: 15px 0;
	height: 80px;
	/*background-color: orange;*/
	display: flex;
	box-sizing: border-box;
	border-bottom: 1px solid #eee;
}

.tab-content .list .left-img {
	flex: 0 0 50px;
	/*background-color: red;*/
	margin-right: 15px;
}

.tab-content .list .left-img img {
	width: 50px;
	height: 50px;
	border-radius: 12px;
}

.tab-content .list .right-txt {
	flex: 1;
	/*background-color: pink;*/
	position: relative;
}

.tab-content .list .right-txt .t1 {
	height: 20px;
	color: #000000;
	font-size: 14px;
}

.tab-content .list .right-txt .t2 {
	height: 17px;
	font-size: 12px;
	color: #333;
}

.tab-content .list .right-txt .t3 {
	font-size: 10px;
	color: #3AA0F6;
}

.tab-content .list .right-txt .t4 {
	width: 58px;
	height: 30px;
	background: #3AA0F6;
	color: #fff;
	text-align: center;
	line-height: 30px;
	font-family: "微软雅黑", "宋体";
	font-size: 14px;
	border-radius: 3px;
	position: absolute;
	top: 50%;
	right: 0;
	margin-top: -15px;
}
</style>



