import Vue from 'vue'
import Router from 'vue-router'
import Iphone from '@/components/Iphone'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Iphone',
      component: Iphone
    }
  ]
})



